<template lang="html">
  <div ref="good" class="suwis-good-details">
    <van-row justify="center" align="center" class="nav">
      <van-col v-for="nav in navlist" span="8" :class="{active:nav.selected}" v-scroll-to="`#${nav.key}`">{{nav.name}}</van-col>
    </van-row>
    <!--  -->
    <van-swipe :id="navlist[0].key" class="banner" ref="banner" :autoplay="3000" indicator-color="#E83F44">
      <van-swipe-item>
        <img :src="details.img" alt="">
      </van-swipe-item>
      <van-swipe-item v-for="item in details.stand">
        <img :src="item.img" alt="">
      </van-swipe-item>
    </van-swipe>
    <!--  -->
    <good-status :details="details" :type="$route.query.type||''"></good-status>
    <!--  //-->
    <!--  -->
    <van-row>
      <van-col span="24" class="title">
        {{details.title}}
      </van-col>
    </van-row>
    <template v-if="details.type!=2">
      <van-row class="price" v-if="details.type!=4">
        <van-col>
          <em class="gray">{{details.price_max}}</em><em>{{details.price_min}}</em>
        </van-col>
        <van-col>
          <span v-if="details.type==2">
            {{details.price_count}}
          </span>
          <span v-else>
            {{details.sell}}
          </span>
        </van-col>
      </van-row>
      <van-row class="coupon">
        <van-col span="24">
          &nbsp;&nbsp;<span @click="couponsVisible=true" v-for="item in coupons" v-if="item.number_can>0&&item.sta==1">{{`${item.total}${item.sum}`}}</span>
          <i v-if="coupons.length==0"></i>
        </van-col>
      </van-row>
    </template>
    <template v-else>
      <van-row>
        <van-col style="color:#E83F44;">*{{details.lowest_price}}</van-col>
      </van-row>
    </template>
    <van-cell class="interval" title="" is-link :value="current.selectedSkuComb.name" @click="showSku('hideSku')"/>
    <van-cell class="interval" title="" :value="details.store&&details.store.site" />
    <van-cell title=""  :value="details.postage==0?'':details.postage+''" />
    <van-cell>
      <span slot="title"><img class="security" src="@/assets/details/security@3x.png" alt="">7</span>
    </van-cell>
    <!--  -->
    <van-cell ref="comment" :id="navlist[1].key" class="interval comment">
      <span slot="title">{{details.evaluate_count}}</span>
      <span v-if="details.evaluate_count>0">&nbsp;<em>{{details.feedback * 100}}%</em></span>
    </van-cell>
    <comment-list v-if="details.evaluate">
      <!-- :date="$moment(item.evaluate_time+1000).format('YYYY-mm-DD HH:MM:SS')" -->
      <comment-item v-for="(item,idx) in details.evaluate"
        v-if="idx<3"
        :name="item.nickname"
        :avatar="item.photo"
        :score="((item.evaluate_express + item.evaluate_serve + item.evaluate_quality) / 15) * 5"
        :content="item.evaluate"
        :medias="JSON.parse(item.evaluate_img)||[]">
      </comment-item>
      <van-row v-if="details.evaluate.length==0" type="flex" align="center" justify="center">
        <van-col style="color:#999999;">
          ~
        </van-col>
      </van-row>
    </comment-list>
    <!--  //-->
    <router-link v-if="details.evaluate&&details.evaluate.length>=2" class="comment-more" :to="{ path: '/goods/comment-list', query: {id: details.id} }"><img src="@/assets/details/more@3x.png" alt=""></router-link>
    <!--  -->
    <div ref="content" :id="navlist[2].key">
      <van-tabs class="good-tabs" v-model="goodTabIdx">
        <van-tab title="">
          <div v-if="details.details" class="good-info interval content">
            <video x5-video-player-type="h5" x5-video-player-fullscreen="true" x-webkit-airplay="allow" webkit-playsinline playsinline v-if="details.details[0].content.video" :src="details.details[0].content.video" controls></video>
            <img :src="details.details[0].content.img" alt="">
            <div v-html="details.details[0].content.editor"></div>
            <span class="no-data"></span>
          </div>
        </van-tab>
        <van-tab title="">
          <div v-if="details.details" class="specification content">
            <img :src="details.details[1].content.img" alt="">
            <div v-html="details.details[1].content.editor"></div>
          </div>
        </van-tab>
        <van-tab title="">
          <div v-if="details.details" class="guarantee content">
            <img :src="details.details[2].content.img" alt="">
            <div v-html="details.details[2].content.editor"></div>
          </div>
        </van-tab>
      </van-tabs>
    </div>
    <!--  //-->
    <!--  -->
    <van-row class="interval shop" v-if="details.store">
      <van-col>
        <dl>
          <dt>
            <img :src="details.store.logo" alt="">
          </dt>
          <dd>
            <h6>{{details.store.name}}</h6>
            <span><em>{{details.store_count}}</em></span>
          </dd>
        </dl>
      </van-col>
      <van-col class="link">
        <router-link :to="{ path: '/shop', query: {id: details.store.id } }">
          <img class="btn" src="@/assets/details/look@3x.png" alt="">
        </router-link>
      </van-col>
    </van-row>
    <!--  //-->
    <!--  -->
    <van-sku
      v-model="skuVisible"
      stepper-title=""
      :sku="sku"
      :goods="skugoods"
      :goods-id="details.id"
      :hide-stock="sku.hide_stock"
      :quota="0"
      :quota-used="0"
      reset-stepper-on-hide
      :show-soldout-sku="true"
      :initial-sku="initialSku"
      @buy-clicked="skuConfirm"
    >
      <!--  sku-header-price -->
      <template slot="sku-header-price" slot-scope="props">
        <div class="van-sku__goods-price">
          <span class="van-sku__price-symbol"></span><span class="van-sku__price-num">{{ props.price }}</span>
        </div>
      </template>
      <!--  -->
      <template slot="sku-stepper" v-if="details.type==2" slot-scope="props">
        <van-row type="flex" justify="space-between" align="center">
          <van-col></van-col>
          <van-col><van-stepper v-model="current.selectedNum" disabled /></van-col>
        </van-row>
      </template>
      <!--  -->
      <div slot="sku-messages"></div>
      <!--  sku actions -->
      <template slot="sku-actions" slot-scope="props">
        <div class="van-sku-actions" style="width:86vw;margin:auto;padding:3.9vw 0;">
          <!--  sku  onBuyClicked  -->
          <van-button
            square
            size="large"
            type="primary"
            @click="props.skuEventBus.$emit('sku:buy')"
          >
          
          </van-button>
        </div>
      </template>
    </van-sku>
    <!--  //-->
    <!--  //-->
    <coupon-list v-model="couponsVisible" title="">
      <coupon-item
        v-for="item in coupons"
        :item="item"
        :title="item.title"
        :desc="`${item.total}${item.sum}`"
        :price="item.sum"
        :time="item.end_time"
        :type="(item.number_can>0&&Date.now()<item.end_time*1000)||item.type==0?1:0"
        @click="receiveCoupon(item)">
        <span slot="btn-text" v-if="item.type==0">
          <template v-if="item.number_can<=0"></template>
          <template v-else>
            {{item.get_sta==0?'':''}}
          </template>
        </span>
        <span slot="btn-text" v-else>
          <template v-if="item.number_can<=0"></template>
          <template v-else>
            <template v-if="Date.now()>item.end_time*1000"></template>
          </template>
          <template v-if="item.number_can>0&&Date.now()<item.end_time*1000">
            {{item.get_sta==0?'':''}}
          </template>
        </span>
        <span slot="time" v-if="item.type==0">
          {{item.day}}
        </span>
      </coupon-item>
    </coupon-list>
    <!--  -->
    <!--  -->
    <share v-model="shareVisible"></share>
    <!--  //-->
    <!--  -->
    <van-goods-action v-if="details.type!=2">
      <van-goods-action-mini-btn
        icon="chat-o"
        text=""
        @click.native="$router.push({path: '/mine/message/getsm', query: {store_id: $route.query.id}})"
      />
       <van-goods-action-mini-btn
        v-if="carNum>0"
        :info="carNum"
        icon="cart-o"
        text=""
        @click.native="$router.push({path: '/goods/shopping-cart', query: {store_id: $route.query.id}})"
      />
      <van-goods-action-mini-btn
        v-else
        icon="cart-o"
        text=""
        @click.native="$router.push({path: '/goods/shopping-cart', query: {store_id: $route.query.id}})"
      />
      <van-goods-action-big-btn
        text=""
        @click.native="showSku('addcar')"
      />
      <van-goods-action-big-btn
        :disabled="disabledSubmit"
        primary
        text=""
        @click.native="!current.selectedSkuComb.id?showSku('buy'):buy(current)"
      />
    </van-goods-action>
    <auction ref="auction" v-else :details="details" :current="current"></auction>
   <!--  -->
  </div>
</template>

<script>
import {
  Toast
} from 'vant'
import _ from 'lodash'
// 
import goodStatus from './good-status'
import auction from './auction'
const $raf = window.requestAnimationFrame || window.webkitRequestAnimationFrame || window.mozRequestAnimationFrame || window.oRequestAnimationFrame || window.msRequestAnimationFrame || function(callback) {
  window.setTimeout(callback, 1000 / 60)
}
import md5 from 'md5'
export default {
  components: {
    goodStatus,
    auction
  },
  data() {
    return {
      navlist: [{
        name: '',
        key: 'good',
        selected: true
      }, {
        name: '',
        key: 'comment',
        selected: false
      }, {
        name: '',
        key: 'content',
        selected: false
      }],
      timer: {},
      lockscroll: false,
      // 
      actionType: '',
      goodTabIdx: 0,
      // 
      details: {},
      // 
      coupons: [],
      // 
      skuVisible: false,
      // sku
      initialSku: {
        // skuKeyStrsku  key 
        // skuValueId id
        s1: '30349',
        s2: '1193',
        // 
        selectedNum: 1
      },
      // 
      skugoods: {
        title: '',
        picture: ''
      },
      priceKey: ['price', 'push_price', 'auction_price', 'price', 'clearance_price'],
      // 
      sku: {
        // sku
        // 
        tree: [],
        //  sku M  sku S 
        list: [],
        price: 0, // 
        stock_num: 0, // 
        collection_id: 0, //  skuId  collection_id sku  id
        none_sku: false, // 
        messages: [],
        hide_stock: false // 
      },
      // 
      carNum: 0,
      // 
      current: {
        selectedSkuComb: {
          name: ''
        }
      },
      couponsVisible: false,
      shareVisible: false,
      // 
      disabledSubmit: false
    }
  },
  created() {
    this.getDetails()
    this.getCoupons()
    this.getCarList()
    // 
    this.$store.commit('core/header', {
      title: '',
      buttons: {
        right: {
          fontSize: '27px',
          text: '\ue655',
          onclick: () => {
            this.shareVisible = true
          }
        }
      }
    })
  },
  mounted() {
    window.addEventListener('scroll', this.checkScroll, this)
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.checkScroll, this)
  },
  watch: {
    // 
    details(val) {
      // 
      this.checkTimeout()
      // 'header_two_label', 'header_three_label'
      let keys = ['header_one_label']
      for (let key of keys) {
        if (val.stand.length == 0) break
        if (val.stand[0][key] != '' && val.stand[0][key]) {
          this.sku.tree.push({
            k: val.stand[0][key],
            v: [],
            k_s: 's1'
          })
        }
      }
      // 
      for (let item of val.stand) {
        for (let idx in this.sku.tree) {
          item.header_one ? '' : item.header_one = ''
          item.header_two ? '' : item.header_two = ''
          this.sku.tree[idx].v.push({
            id: item.id,
            name: item.header_one + ' ' + item.header_two,
            imgUrl: item.img
          })
          this.sku.list.push({
            ...item,
            id: this.details.id,
            price: item[this.priceKey[val.type || 0]] * 100,
            s1: item.id,
            stock_num: item.count,
            name: item.header_one + ' ' + item.header_two,
          })
        }
      }
      // 
      this.sku.price = this.details.price_min
      this.sku.stock_num = this.details.inventory
      this.skugoods.title = this.details.title
      this.skugoods.picture = this.details.img
    }
  },
  methods: {
    // 
    checkScroll() {
      let sy = window.scrollY
      let offsetTops = []
      let idx = 0
      for (let nav of this.navlist) {
        nav.selected = false
        offsetTops.push(this.$refs[nav.key].offsetTop - 51)
      }
      if (sy < offsetTops[1]) idx = 0
      if (sy > offsetTops[1] && sy < offsetTops[2]) idx = 1
      if (sy > offsetTops[2]) idx = 2
      if (idx == 0) {
        setTimeout(() => {
          window.cancelAnimationFrame(this.timer)
        }, 680)
      }
      this.navlist[idx].selected = true
    },
    // 
    checkTimeout() {
      //  && 
      if (this.details.type == 1 || this.details.type == 2) {
        this.disabledSubmit = (this.details.activity_end_time || 0) * 1000 - Date.now() < 0
        if (!this.disabledSubmit) {
          setTimeout(() => {
            this.checkTimeout()
          }, 1000)
        }
      }
    },
    // 
    async getDetails() {
      let res = await this.$axios.post('goods/find', {
        id: this.$route.query.id
      })
      this.details = res.data.data || {}
    },
    // 
    async getCoupons() {
      let res = await this.$axios.post('goods/coupons', {
        goods_id: this.$route.query.id
      })
      this.coupons = res.data.data || []
    },
    // 
    async receiveCoupon(item) {
      let res = await this.$axios.post('goods/getcoupon', {
        coupon_id: item.id
      })
      if (res.data.code == 1) {
        Toast('')
        // 
        this.getCoupons()
      } else {
        Toast(res.data.msg)
      }
      this.couponsVisible = false
    },
    // 
    showSku(type) {
      // 
      if (!this.$store.getters['core/logined']) {
        Toast('')
        this.$router.push('/login')
        return
      }
      this.skuVisible = true
      this.actionType = type
    },
    // 
    hideSku(evt) {
      this.skuVisible = false
    },
    // 
    async skuConfirm(evt) {
      this[this.actionType](evt)
      // 
      this.current = evt
    },
    // 
    async buy(evt) {
      this.$router.push({
        path: '/uc/orders/confirm-order',
        query: {
          stand_id: evt.selectedSkuComb.s1,
          num: evt.selectedNum
        }
      })
    },
    // 
    async addcar(evt) {
      let res = await this.$axios.post('car/add', {
        stand_id: evt.selectedSkuComb.s1,
        num: evt.selectedNum,
        goods_id: this.$route.query.id,
        store_id: this.details.store.id
      })
      if (res.data.code == 1) {
        Toast('')
        this.hideSku(evt)
        // 
        this.getCarList()
      } else {
        Toast(res.data.msg)
      }
    },
    // 
    async getCarList(evt) {
      let res = await this.$axios.post('car/list')
      // 
      let shops = res.data.data || {}
      this.carNum = 0
      for (let key in shops) {
        let shop = shops[key]
        for (let good of shop.goods) {
          this.carNum += 1
        }
      }
    },
    // 
    showKeyboard() {
      this.hideSku()
      this.$refs.auction.showKeyboard()
    }
  }
}
</script>
<style lang="scss">
.suwis-good-details {
    .good-tabs {
        .van-tabs__line {
            display: none;
        }
        .van-tab {
            color: $gray;
        }
        .van-tab--active {
            color: $red;
        }
    }

    .van-sku-header__img-wrap {

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
</style>
<style lang="scss" scoped>
.suwis-good-details {
    position: relative;
    padding-bottom: calc(50px + 86px);
    padding-top: 50px;
    font-size: 12px;
    .nav {
        height: 50px;
        align-items: center;
        display: flex;
        font-size: 16px;
        position: fixed;
        left: 0;
        top: 0;
        width: 100vw;
        background: #fff;
        z-index: 100;

        .van-col {
            position: relative;
            text-align: center;
        }

        .active::before {
            content: '';
            position: absolute;
            width: 32px;
            margin-left: -16px;
            left: 50%;
            bottom: 0;
            border-bottom: 2px solid $red;
        }
    }

    .banner {
        height: 100vw;
        max-width: 100vw;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .van-row {
        .van-col {
            padding: 10px 15px;
        }
    }

    .van-cell {
        white-space: nowrap;
        .van-cell__title {
            text-align: left;
        }
    }
    .title {
        font-size: 16px;
        color: $primary;
    }
    .price {
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: $gray;
        .van-col {
            flex: 1;
        }
        em {
            color: $red;
            font-style: normal;
            font-size: 16px;
        }
        .gray {
            text-decoration: line-through;
            color: $gray;
            border-right: 1px solid $gray;
            padding-right: 10px;
            margin-right: 10px;
            height: 16px;
            display: inline-block;
            line-height: 16px;
        }
        span {
            color: $gray;
            font-size: 12px;
            display: block;
            text-align: right;
        }
    }
    .coupon {
        span {
            padding: 5px 8px;
            transform: scale(0.82);
            transform-origin: left;
            display: inline-block;
            color: $red;
            background: url("./../../assets/details/coupon@3x.png") no-repeat center/auto 100%;
            border: 1px solid rgba(232,63,68,0.39);
            border-radius: 2px;
            border-top: none;
            border-bottom: none;
        }
        i {
            font-size: 12px;
            font-style: normal;
            transform: scale(0.89);
            transform-origin: left;
            display: inline-block;
            color: $gray;
        }
    }
    .security {
        max-width: 15px;
        vertical-align: middle;
        display: inline-block;
        margin-right: 5px;
        margin-top: -2px;
    }
    .interval {
        border-top: 6px solid $border;
    }
    .shop {
        position: fixed;
        bottom: 50px;
        width: 100vw;
        height: 86px;
        left: 0;
        background: #fff;
        display: flex;
        justify-content: space-between;
        border-bottom: 2px solid #EFEFEF;
        margin-bottom: -2px;
        z-index: 399;

        .van-col {
            display: flex;
            align-items: center;
        }

        dl {
            display: flex;
            align-items: center;

            dd,
            dt {
                margin: 0;
            }
            dd {
                padding-left: 10px;
            }

            img {
                width: 44px;
                height: 44px;
                object-fit: cover;
                border-radius: 4px;
                display: block;
            }

            h6 {
                color: $primary;
                margin: 0;
                padding-bottom: 5px;
                font-size: 12px;
            }

            span {
                color: $gray;
            }

            em {
                font-style: normal;
                color: $red;
            }
        }
        .link {
            justify-content: center;
            .btn {
                max-width: 68px;
            }
        }
    }
    .comment {
        em {
            color: $red;
            font-style: normal;
            font-size: 14px;
        }
    }
    .comment-more {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 20px;
        color: $gray;
        img {
            max-width: 15px;
            margin-left: 5px;
        }
    }
    //
    .good-info {
        padding: $interval;
        video {
            width: 100%;
            margin-bottom: 2.8vw;
            background: #000;
        }
        .no-data {
            color: $gray;
            text-align: center;
            display: block;
            padding: $interval;
        }
    }
    //
    .specification {
        padding: $interval;
        line-height: 26px;
        padding-bottom: 20px;
        min-height: calc(100vh - 239px);
        table {
            width: 100%;
            border-collapse: collapse;
            color: $gray;
            td {
                border: 1px solid $border;
                padding: 0 8px;

                &:first-child {
                    width: 99px;
                }
            }
        }
    }
    .guarantee {
        padding: $interval;
        line-height: 26px;
        min-height: calc(100vh - 239px);
    }
    .content {
        max-width: 100vw;
        overflow: hidden;
    }
    //
    .van-goods-action {
        background: transparent;
        z-index: 399;
        .van-goods-action-mini-btn {
            background: transparent;
        }
        .van-button--warning {
            background: linear-gradient(54deg,rgba(250, 165, 55, 1) 0%,rgba(247, 149, 50, 1) 17%,rgba(240, 107, 37, 1) 100%);
            border: none;
        }
        .van-button--danger {
            background: linear-gradient(54deg,rgba(245,92,60,1) 0%,rgba(246,96,62,1) 17%,rgba(221,11,17,1) 100%);
            border: none;

            &[disabled] {
                background: #b4b4b4;
                opacity: 1;
            }
        }
        &::before {
            content: '';
            width: 100vw;
            background: #fff;
            position: absolute;
            height: 100%;
            left: 0;
            top: 2px;
        }
    }
}
</style>
