<template lang="html">
<div class="suwis-auction" :class="{disable}">
  <!--  -->
  <div class="deposit-info" v-if="auctionShow">
    <dl class="price">
      <dt>
        <span></span>
        <span>
          <em>{{(current.selectedSkuComb.price*0.001).toFixed(2)}}</em>
        </span>
      </dt>
      <dd>
        <img src="@/assets/details/hnit@2x.png" alt="">
      </dd>
    </dl>
    <van-cell value="15.0" />
    <van-cell-group v-if="adres.id">
      <van-cell :value="`${adres.name||''}(${adres.tel||''})`" />
      <van-cell :value="`${adres.city||''}${adres.area||''}${adres.address||''}`" @click="chooseAdres" is-link />
    </van-cell-group>
    <van-cell-group v-else>
      <van-cell value="" is-link @click="chooseAdres"></van-cell>
    </van-cell-group>
    <van-cell value="" is-link @click="$router.push('/special/auctionrlue')">
      <template slot="title">
        <div style="white-space: nowrap;" @click.stop>
          <van-checkbox v-model="agreeAgreement"></van-checkbox>
        </div>
      </template>
    </van-cell>
    <van-cell>
      7
    </van-cell>
    <van-goods-action>
      <van-goods-action-big-btn
        primary
        text=""
        @click.native="showPayTypeShow"
      />
     </van-goods-action>
  </div>
  <!--  -->
  <van-goods-action>
    <van-goods-action-mini-btn
      icon="chat-o"
      text=""
      @click.native="$router.push({path: '/mine/message/getsm', query: {store_id: $route.query.id}})"
    />
    <van-goods-action-mini-btn
      icon="shop-o"
      text=""
      @click.native="$router.push({path: '/shop', query: {id: details.store.id }})"
    />
    <van-goods-action-big-btn
      primary
      :text="details.isauction==1?'': ` (${(details.stand[0].auction_price * 0.1 || 0).toFixed(2)} )`"
      @click.native="!current.selectedSkuComb.id?$parent.showSku('showKeyboard'):showKeyboard()"
    />
   </van-goods-action>
  <!--  -->
  <!--  //-->
  <!--  -->
  <van-actionsheet
    v-model="payTypeShow"
    title=""
    class="suwis-pay-type"
    :close-on-click-overlay="false">
    <van-cell-group>
      <van-cell title=""></van-cell>
      <van-radio-group v-model="payType">
        <van-radio style="padding-left:15px;" v-for="pay in typeList"
          :name="pay.id"
          :key="pay.id">
          <div class="paytype-check-line">
            <span class="option">
              <img :src="pay.icon" class="pay-image">
              {{pay.description}}
            </span>
            <span class="balance-sum" v-show="pay.id === 'balancepay'">{{balanceSum}}</span>
          </div>
        </van-radio>
      </van-radio-group>
      <div class="paytype-button-line">
        <div class="deploy" @click="showPayboard"></div>
      </div>
    </van-cell-group>
  </van-actionsheet>
  <!--  //-->
  <!--   -->
  <van-actionsheet
    title=""
    v-model="keyboardShow"
    :close-on-click-overlay="false"
    @cancel="keyboardShow = false">
    <div class="keyboard-text" @click.stop>
      <van-field v-model="keyboardText" input-align="center" :placeholder="`${details.price_max+current.selectedSkuComb.lowest_price||0}`" readonly />
    </div>
    <van-number-keyboard
      :show="true"
      extra-key="."
      theme="custom"
      close-button-text=""
      @input="keyboardInput"
      @delete="keyboardDelete"
      @close="choosePaytype"
    />
   </van-number-keyboard>
 </van-actionsheet>
 <!--  //-->
 <!--  -->
 <van-actionsheet
   title=""
   v-model="payboardShow"
   :close-on-click-overlay="false"
   @cancel="payboardShow = false">
   <van-password-input :value="paypass"/>
   <div class="link-line">
     <router-link to="/resetpaypwd" class="forget-password"></router-link>
   </div>
   <van-number-keyboard
     :show="true"
     @input="passwordInput"
     @delete="passwordDelete"
   />
 </van-actionsheet>
 <!--  //-->
</div>
</template>

<script>
import md5 from 'md5'
export default {
  props: ['details', 'current'],
  data() {
    return {
      disable: false,
      timer: {},
      adres: {},
      typeList: [{
        id: 'balancepay',
        description: '',
        icon: require('./../../assets/orders/score-pay@2x.png')
      }],
      agreeAgreement: false,
      // 
      keyboardText: '',
      keyboardArray: [],
      payType: '',
      // 
      paypass: '',
      paypassArray: [],
      // 
      balanceSum: 0,
      // 
      payTypeShow: false,
      // 
      keyboardShow: false,
      // 
      payboardShow: false,
      // 
      auctionShow: false
    }
  },
  watch: {
    keyboardArray(val) {
      this.keyboardText = val.join('')
    },
    paypassArray(val) {
      this.paypass = val.join('')
    },
    payboardShow(val) {
      if (val) this.paypassArray = []
      this.payTypeShow = false
    },
    payTypeShow(val) {
      if (val) this.keyboardShow = false
    },
    keyboardShow(val) {
      if (val) this.keyboardArray = []
    },
    auctionShow(val) {
      //
    },
    current(val) {
      // sessionStorage
    }
  },
  mounted() {
    // sessionStorage
    let current = JSON.parse(window.sessionStorage.getItem('details_sku_current'))
    if (current && this.details.type == 2) {
      this.$parent.current = current
      this.showKeyboard()
      // 
      window.sessionStorage.removeItem('details_sku_current')
    }
    // 
    this.$route.query.address_id ? this.getAdres() : this.getAdresList()
    // 
    this.timer = setInterval(() => {
      this.disable = this.details.activity_end_time * 1000 < Date.now()
    }, 1000)
  },
  beforeDestroy() {
    // 
    window.clearInterval(this.timer)
  },
  methods: {
    async getAdresList() {
      let res = await this.$axios.post('/user/alladdress')
      let list = res.data.data || []
      for (let item of list) {
        if (item.sta) this.adres = item
      }
      // 
      if (!this.adres.id) this.adres = list[0]
    },
    // 
    async getAdres() {
      let res = await this.$axios.post('/user/address', {
        id: this.$route.query.address_id
      })
      this.adres = res.data.data || {}
    },
    // 
    showKeyboard() {
      this.getBalance()
      // 
      if (this.details.isauction == 1) {
        this.keyboardShow = true
      } else {
        // 
        this.keyboardText = this.current.selectedSkuComb.price * 0.001
        this.auctionShow = true
      }
    },
    // 
    keyboardInput(num) {
      this.keyboardArray.push(num)
    },
    // 
    keyboardDelete() {
      this.keyboardArray.pop()
    },
    // 
    passwordInput(num) {
      this.paypassArray.push(num)
      if (this.paypassArray.length == 6) {
        this.payboardShow = false
        setTimeout(() => {
          this.auction()
        }, 399)
      }
    },
    // 
    passwordDelete() {
      this.paypassArray.pop()
    },
    // 
    choosePaytype() {
      this.details.isauction == 1 ? this.addprice() : this.payTypeShow = true
      this.keyboardShow = false
    },
    // 
    chooseAdres() {
      // sessionStorage
      window.sessionStorage.setItem('details_sku_current', JSON.stringify(this.current))
      let query = this.$route.query
      delete query.address_id
      // 
      this.$router.push({
        path: '/uc/setting/address',
        query: {
          from: '/goods/details?id=96&showauction=true',
          ...query
        }
      })
    },
    // 
    showPayTypeShow() {
      if (!this.adres.id) {
        this.$toast('')
        return
      }
      if (!this.agreeAgreement) {
        this.$toast('')
        return
      }
      this.payTypeShow = true
    },
    // 
    showPayboard() {
      this.payboardShow = true
    },
    // 
    async getBalance() {
      // 
      let res = await this.$axios.post('/mine/mycommission')
      this.balanceSum = res.data.data || 0
    },
    // 
    async auction(evt) {
      let res = await this.$axios.post('goods/auction', {
        goods_id: this.current.goodsId,
        stand_id: this.current.selectedSkuComb.s1,
        pay_type: 'balancepay',
        address_id: 5,
        paypass: md5(this.paypass)
      })
      if (res.data.code == 1) {
        this.$toast('')
        this.auctionShow = false
        setTimeout(() => {
          window.location.reload()
        }, 1200)
      } else {
        this.$toast(res.data.msg)
      }
    },
    // 
    async addprice() {
      if (this.keyboardText < this.current.selectedSkuComb.lowest_price) {
        this.$toast(`${this.current.selectedSkuComb.lowest_price}`)
        return false
      }
      let res = await this.$axios.post('goods/auction_addprice', {
        goods_id: this.current.goodsId,
        stand_id: this.current.selectedSkuComb.s1,
        // pay_tpye: 'balancepay',
        price: this.keyboardText,
        // paypass: md5(this.paypass)
      })
      if (res.data.code == 1) {
        this.$toast('')
      } else {
        this.$toast(res.data.msg)
      }
    }
  }
}
</script>

<style lang="scss">
.suwis-auction {
    position: absolute;
    z-index: 999;
    left: 0;

    .deposit-info {
        position: fixed;
        background: #fff;
        left: 0;
        top: 0;
        width: 100vw;
        height: 100vh;
        z-index: 999;
        padding: 0;
        .price {
            margin: 0;
            background: linear-gradient(54deg,rgba(243,90,90,1) 0%,rgba(246,96,62,1) 17%,rgba(221,11,17,1) 100%);
            color: #fff;
            padding: 15px;

            dt {
                display: flex;
                justify-content: space-between;
                align-items: flex-end;
                border-bottom: 1px solid #fff;
                padding-bottom: 2vw;
                margin-bottom: 3vw;
                span {
                    font-size: 1.2rem;
                }
                em {
                    font-style: normal;
                    font-size: 2.6rem;
                    font-weight: bold;
                }
            }

            dd {
                padding: 0;
                margin: 0;
                display: flex;
                align-items: center;
                font-size: 14px;

                img {
                    display: block;
                    max-width: 18px;
                    margin-right: 5px;
                }
            }
        }
    }

    .keyboard-text {
        width: 100vw;
        margin-bottom: 216px;
        background: #fff;
        padding: 10px;
    }

    .van-number-keyboard {
        z-index: 9999 !important;
    }
    //
    .paytype-check-line {
        display: flex;
        align-items: center;
        justify-content: space-between;
        line-height: 50px;
        .pay-image {
            width: 24px;
            height: 24px;
            margin-right: 10px;
        }
        .balance-sum {
            color: #f0914b;
            font-size: 12px;
        }
        .option {
            display: flex;
            align-items: center;
        }
    }

    .paytype-button-line {
        padding: 30px 26px 24px;
        border-top: 1px solid #f5f5f5;
        .deploy {
            height: 45px;
            margin: 0 auto;
            background: linear-gradient(54deg,rgba(245,92,60,1) 0%,rgba(246,96,62,1) 17%,rgba(221,11,17,1) 100%);
            border-radius: 25px;
            color: #fff;
            cursor: pointer;
            font-size: 16px;
            line-height: 45px;
            text-align: center;
        }
    }

    .link-line {
        margin: 0 20px 250px;
        text-align: right;

        a {
            color: #f0914b;
            position: relative;
            top: 18px;
        }
    }
    // 
    &.disable {
        .van-button {
            pointer-events: none;
            background: #ccc;
            border-color: #ccc;
        }
    }
}
</style>
